@import "variables.scss";
/* ----------------Cleaning up styles------------------ */
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  line-height: 22px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  &::-moz-focus-inner {
    padding: 0;
    border: 0;
  }
}

a,
a:visited {
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

/* --------------------------------------------------- */
* {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: $gray;
}
body {
  text-align: center;
}
header {
  display: flex;
  justify-content: center;
  position: relative;
  width: 100%;
}
section {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  max-width: 1220px;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}
.title {
  font-size: 35px;
  color: $black;
  text-align: center;
}
